___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --bg: #0b0b0b;
  --fg: #f5f5f5;
  --muted: #a3a3a3;
  --stroke: #333;
  --radius: 10px;
  --gap: 16px;
  color-scheme: dark
}

*,
*::before,
*::after {
  box-sizing: border-box
}

html,
body {
  height: 100%
}

body {
  margin: 0;
  background: var(--bg);
  color: var(--fg);
  font: 15px/1.6 "Courier New", Courier, monospace
}

/* Layout */
.container {
  display: grid;
  grid-template-columns: 340px 1fr;
  gap: 24px;
  align-items: start;
  padding: 16px
}

.container>* {
  min-width: 0
}

/* prevent grid blowout */
@media (max-width:900px) {
  .container {
    grid-template-columns: 1fr
  }
}

.sidebar {
  display: grid;
  gap: 14px;
  align-content: start
}

.preview {
  min-width: 0
}

/* Shared surface */
.controls,
.status,
.col,
.log,
img,
canvas,
a {
  border: 1px solid var(--stroke);
  border-radius: var(--radius)
}

img,
canvas {
  display: block;
  width: 100%;
  height: auto;
  background: var(--bg);
  max-height: 85vh;
  object-fit: contain
}

/* Header */
.page-header {
  display: grid;
  gap: 6px
}

.page-header h1 {
  margin: 0;
  font-size: clamp(1.1rem, 1rem + 1vw, 1.6rem);
  font-weight: 700
}

.subtitle {
  margin: 0;
  color: var(--muted);
  font-size: .95rem
}

/* Controls */
.controls {
  display: grid;
  gap: 12px;
  padding: 12px;
  background: rgba(255, 255, 255, .02);
  grid-auto-rows: min-content
}

.control {
  display: grid;
  gap: 6px
}

.label {
  font-size: .85rem;
  color: var(--muted)
}

input[type=file],
select,
button,
a {
  background: transparent;
  color: var(--fg);
  font-size: 14px;
  padding: 10px 12px;
  border: 1px solid var(--stroke);
  border-radius: var(--radius);
  max-width: 100%
}

button {
  cursor: pointer
}

button:hover:not(:disabled),
a:hover {
  background: var(--fg);
  color: var(--bg)
}

button:disabled {
  opacity: .6;
  cursor: not-allowed
}

/* File input: truncate long filenames */
/* Replace your file input block with this minimal set */
input[type=file] {
  width: 100%;
  max-width: 100%;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden
}

input[type=file]:hover,
input[type=file]:focus {
  overflow: auto
}

/* reveals full name via horizontal scroll */

input[type=file]::file-selector-button,
input[type=file]::-webkit-file-upload-button {
  margin-right: 8px;
  padding: 8px 10px;
  border: 1px solid var(--stroke);
  border-radius: var(--radius);
  background: rgba(255, 255, 255, .06);
  color: var(--fg);
  cursor: pointer
}


/* Select (dark popup) */
select {
  background: #111;
  color: var(--fg);
  color-scheme: dark;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  white-space: nowrap
}

select option,
select optgroup {
  background: #111;
  color: var(--fg)
}

select:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, .08) inset
}

/* Status + log */
.status {
  padding: 10px 12px;
  background: rgba(255, 255, 255, .04)
}

.log {
  white-space: pre-wrap;
  font-size: .9rem;
  padding: 8px 10px;
  display: none
}

.log:not(:empty) {
  display: block
}

/* Preview area */
.row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--gap);
  align-items: start
}

@media (max-width:900px) {
  .row {
    grid-template-columns: 1fr
  }
}

.col {
  display: grid;
  gap: 12px;
  padding: 12px;
  background: rgba(255, 255, 255, .02)
}

.col h3 {
  margin: 0;
  font-weight: 700;
  font-size: 1rem
}

/* Utils */
a {
  text-decoration: none;
  display: inline-block
}

.hidden {
  display: none !important
}

/* ===== Compact Palette Board ===== */
:root{
  --pv-tile: 32px;   /* size of each square */
  --pv-gap: 8px;     /* spacing between squares */
  --pv-cols: 8;      /* default columns; change per container if desired */
}

/* Scope the board with data-palette and .pv-compact */
[data-palette].pv-compact {
  /* allow grid to be only as wide as its content (so it doesn't fill the row) */
  display: block;
  width: max-content;
  max-width: 100%;    /* prevent overflow on tiny viewports */
  overflow: hidden;   /* clip if needed */
  padding: 6px;       /* optional inner padding to match your box drawing */
  border-radius: var(--radius);
  border: 1px solid var(--stroke);
  background: transparent;
}

[data-palette].pv-compact .pv-grid{
  display: inline-grid;                                  /* grid sizes to content */
  grid-template-columns: repeat(var(--pv-cols), var(--pv-tile));
  grid-auto-rows: var(--pv-tile);
  gap: var(--pv-gap);
  justify-content: start;                                /* leftover space stays on right */
  align-content: start;
}

[data-palette].pv-compact .pv-item{
  width: var(--pv-tile);
  height: var(--pv-tile);
  border-radius: 4px;
  border: 1px solid var(--stroke);
  padding: 0;
  background: transparent;
  cursor: pointer;
  transition: transform .06s ease, box-shadow .06s ease;
}

[data-palette].pv-compact .pv-item:hover{
  transform: translateY(-1px);
  box-shadow: 0 2px 6px rgba(0,0,0,.25);
}

[data-palette].pv-compact .pv-item:focus-visible{
  outline: 2px solid #4C9AFF;
  outline-offset: 2px;
}

/* we only show the swatch; hide any labels if present */
[data-palette].pv-compact .pv-labels,
[data-palette].pv-compact .pv-name,
[data-palette].pv-compact .pv-rgba,
[data-palette].pv-compact .pv-title { display: none !important; }


/* Small screens: allow smaller min width before wrapping */
@media (max-width: 700px) {
  [data-palette] .pv-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }
}
